import { useAccountStore } from '@cetus/stores'
import { d } from '@cetus/utils'
import { SuiClient } from '@mysten/sui/client'
import { Transaction } from '@mysten/sui/transactions'
import { normalizeSuiAddress } from '@mysten/sui/utils'
import { useSdk } from 'packages/sdk-factory/src'
import { AppendCoinToTip, EstimateFee, ExecuteTransactionBlock, ShioFastRpcUrl } from 'shio-fast-sdk'
import { COIN_WITH_BALANCE, resolveCoinBalance } from './resolveCoinBalance'
import { useSigner } from './useSigner'

export default function useFastModeTransaction() {
  const { signTransactionBlock, executeTransactionBlock, signAndExecuteTransactionBlock } = useSigner()
  const { currentAccount } = useAccountStore()
  const clmm = useSdk('clmm')

  const getFastModeTx = async (tx: any, maxCapForGas: string, customGasPrice: string) => {
    console.log('üöÄüöÄüöÄ ~ useFastModeTransaction.ts:12 ~ getFastModeTx ~ getFastModeTx:', getFastModeTx)
    let result
    let startExecuteTime
    let transactionBlockBytes
    try {
      const client = new SuiClient({ url: ShioFastRpcUrl })
      const clmmClient = clmm!.FullClient

      tx.setSender(currentAccount?.address)
      tx.setGasPrice(Number(customGasPrice))

      const ntx = Transaction.from(tx)
      ntx.addIntentResolver(COIN_WITH_BALANCE, resolveCoinBalance)

      const uesFastEstimateGas = true

      let gasBudget = 0
      let tipAmount = 0
      if (uesFastEstimateGas) {
        const estimatedFee = await EstimateFee({
          transaction: ntx,
          client
        })
        gasBudget = estimatedFee.gasBudget
        tipAmount = estimatedFee.tipAmount
      } else {
        const devResult = await clmmClient.devInspectTransactionBlock({
          transactionBlock: tx,
          sender: currentAccount?.address ?? normalizeSuiAddress('0x0')
        })
        const { gasUsed } = devResult.effects
        const { computationCost, storageCost, storageRebate, nonRefundableStorageFee } = gasUsed
        const actualGasUsed = Number(d(computationCost).add(storageCost).sub(storageRebate).toFixed(0))
        console.log('üöÄ ~ getFastModeTx ~ gasUsed:', {
          gasUsed,
          actualGasUsed
        })
        gasBudget = Number(d(actualGasUsed).mul(2.5).toFixed(0))
        tipAmount = gasBudget
      }

      const maxCapForGasAmount = d(maxCapForGas).mul(Math.pow(10, 9)).toString()
      if (d(gasBudget).gt(maxCapForGasAmount)) {
        throw Error('Max cap reached. This is to prevent your txn from overpaying. You can choose to edit your gas max cap in settings if needed.')
      }

      tx.setGasBudget(gasBudget)

      const tipCoins = tx.splitCoins(tx.gas, [tipAmount])
      console.log('üöÄ ~ getFastModeTx ~ tx:', {
        tx,
        tipAmount,
        gasBudget
      })
      AppendCoinToTip(tx, tipCoins[0], tipAmount)

      const builtTx = await tx.build({
        client: clmmClient
      })

      // const signTx = await signTransactionBlock(tx)
      const signTx = await signTransactionBlock(builtTx)

      console.log('üöÄ ~ file: useTransaction.tsx:94 ~ useTransaction ~ signTx:', signTx)

      const { bytes, signature } = signTx

      transactionBlockBytes = bytes

      console.log('üöÄ ~ getFastModeTx ~ signTx:', signTx)

      startExecuteTime = new Date().getTime()
      // Execute the transaction with Fast Mode.
      // const result = await ExecuteTransactionBlock(client, signature, {
      result = await ExecuteTransactionBlock(client, signTx, {
        showEffects: true,
        showEvents: true
      })
      console.log('üöÄ ~ getFastModeTx ~ result:', result)
      // return result
    } catch (error) {
      console.log('üöÄ ~ getFastModeTx ~ error:', error)
      console.log('üöÄ ~ getFastModeTx ~ String(error).includes(Failed to fetch):', String(error).includes('Failed to fetch'))
      if (String(error).includes('Failed to fetch')) {
        throw Error('Fast Mode service error')
      }
      throw error
    }

    // try {
    //   await ExecuteBundle(client, [signTx])
    // } catch (error) {
    //   console.log('üöÄ ~ getFastModeTx ~ error:', error)
    // }

    return {
      result,
      startExecuteTime,
      transactionBlockBytes
    }
  }

  return {
    getFastModeTx
  }
}
